    ImClient.prototype.heartbeatLoop = function (conn) {
        var _this = this;
        var start = Date.now();
        var loop = function () {
            if (_this.state != State.CONNECTED) {
                Logger_1["default"].error("heartbeatLoop exited");
                return;
            }
            if (Date.now() - start >= heartbeatInterval) {
                Logger_1["default"].info(">>> send ping ;");
                start = Date.now();
                var pingPack = exports.imClient.buildMessagePack(Command_1.SystemCommand.PING, {});
                conn.send(pingPack.pack(false));
            }
            setTimeout(loop, 500);
        };
        setTimeout(loop, 500);
    };
